html,
body {
    padding: 0;
    margin: 0;

    font-family: 'Nunito', sans-serif;
}

a {
    color: inherit;
    text-decoration: none;
}

* {
    box-sizing: border-box;
    font-family: 'Nunito', sans-serif;
}

.containerweb {
    min-height: 100vh;
    height: 100vh;
    padding: 0 1.5rem;
    display: flex;
    flex-direction: column-reverse;
    /* justify-content: center; */
}

.fullw {
    /* background-image: url(../assets/fondo/jor1.jpg); */
    background-position: 75% center;
    background-repeat: no-repeat;
    background-size: cover;
}

.paperMain {
    margin-bottom: 20;
    display: 'flex';
    flex-wrap: 'wrap';
    align-content: 'center';
    align-items: 'center';
    justify-content: 'space-between';
    border-radius: 30;
    padding: 5;
    background-color: 'rgba(184, 31, 140,0.8)';
}
